* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}
body{
    /* background-repeat: no-repeat; */
    background-size: cover;
    background-position: center;
}
body.level1{
    background-image: url('../images/cosmos/с1.jpeg');
}
body.level2{
    background-image: url('../images/cosmos/с2.jpeg');
}
body.level3{
    background-image: url('../images/cosmos/с3.jpeg');
}
body.level4{
    background-image: url('../images/cosmos/с4.jpeg');
}
.game {
    max-width: 600px;
    margin: 0 auto;
    padding: 1rem;
}

.container {
    max-width: 1000px;
    margin: 0 auto;
    padding-left: 15px;
    padding-right: 15px;
}

#arkanoid {
    background-color: #5a67dc;
    width: 100%;
    border: 10px solid rgba(139, 137, 137);
    border-radius: 10px;
}

.anounce {
    position: fixed;
    text-align: center;
    text-transform: uppercase;
    top: -100%;
    left: 50%;
    transform: translate(-50%, -50%);
    opacity: 0;
    font-size: 1.5rem;
    line-height: 110%;
    color: rgb(235, 236, 240);
    background-color: rgb(0, 0, 0, 0.5);
    border-radius: 10px;
    padding: 2rem 1rem;
    font-weight: bold;
    transition: all 0.5s ease-in-out;
    text-shadow: 0 0 5px rgba(255, 254, 253, 0.95);
    z-index: 1000;
}

.anounce.active {
    top: 50%;
    opacity: 1;
}